  mutations: {
    setEntInfo(state, data) {
      state.entInfo.entAccount.accountName = data.tax_id
      state.entInfo.entBasicInfo.entName = data.company_title
      state.entInfo.entBasicInfo.entCode = data.tax_id
      state.entInfo.entBasicInfo.entAddr = data.register_address
      state.entInfo.entBasicInfo.entTrade = data.industry.industry_name
      state.entInfo.entBasicInfo.entPersonName = data.legal_person
      state.entInfo.entBasicInfo.entPersonIDNum = data.legal_person_id
      state.entInfo.entBasicInfo.entPersonTel = data.legal_person_tel
      state.entInfo.entBasicInfo.paymasterName = data.legal_person
      state.entInfo.entBasicInfo.licenseNum = data.tax_id
      state.entInfo.entBasicInfo.licenseDate = data.register_date
    },
    resetAnswer(state) {
      state.answer = {}
    },
    // 修改密码
    modifyPassword(state, mp) {
      state.entInfo.entAccount.password = mp
      state.answer.changePassword = mp
    },
    // 减员
    reduceEntStaffInfo(state, data) {
      if (state.entInfo.entStaffInfo.length > 0) {
        state.entInfo.entStaffInfo.forEach((v, i) => {
          if (v.IDNum === data.id) {
            const el = state.entInfo.entStaffInfo.splice(i, 1)
            // 保存答案
            if (!state.answer.reduceEntStaffInfo) {
              state.answer.reduceEntStaffInfo = []
            }
            state.answer.reduceEntStaffInfo.push(el)
          }
        })
      }
    },
    // 增员
    increaseEntStaffInfo(state, data) {
      state.entInfo.entStaffInfo = state.entInfo.entStaffInfo.concat(data)
      // 保存答案
      if (!state.answer.increaseEntStaffInfo) {
        state.answer.increaseEntStaffInfo = []
      }
      state.answer.increaseEntStaffInfo.push(data)
    },
    // 修改企业职工信息 职工信息
    modifyEntStaffInfo(state, ms) {
      ms.forEach(e => {
        state.entInfo.entStaffInfo.forEach((value, index) => {
          if (value.IDNum === e.IDNum) {
            state.entInfo.entStaffInfo[index] = e
          }
        })
      })
    },
    // 更新企业职工工资情况
    updateEntStaffWageStatus(state, sw) {
      sw.forEach(e => {
        state.entInfo.entStaffInfo.forEach((value, index) => {
          if (value.IDNum === e.IDNum) {
            state.entInfo.entStaffInfo[index] = e
          }
        })
      })

      // 更新答案
      state.answer.entStaffInfo = state.entInfo.entStaffInfo
    },
    // 更新企业人员增减情况
    updateEntStaffChange(state, data) {
      state.entInfo.entStaffChange = state.entInfo.entStaffChange.concat(data)

      // 保存答案
      if (!state.answer.entStaffChange) {
        state.answer.entStaffChange = []
      }
      state.answer.entStaffChange.push(data)
    },
    // 企业缴费情况
    entChargeStatus(state, cs) {
      cs.forEach(e => {
        state.entInfo.entChargeStatus.push(e)
        // 更新答案
        if (!state.answer.entChargeStatus) {
          state.answer.entChargeStatus = []
        }
        state.answer.entChargeStatus.push(e)
      })

    },
    // 修改企业发票邮寄地址信息
    modifyEntIMA(state, ia) {
      state.entInfo.entInvoiceMailingAddr = ia
    },
    // 劳动用工备案 新增
    entAddStaff(state, data) {
      state.entInfo.entAddStaff = state.entInfo.entAddStaff.concat(data)

      // 更新答案
      if (!state.answer.entAddStaff) {
        state.answer.entAddStaff = []
      }
      state.answer.entAddStaff.push(data)
    },
    // 劳动用工备案 修改
    updateEntAddStaff(state, data) {
      if (state.entInfo.entAddStaff.length > 0) {
        state.entInfo.entAddStaff.forEach(v => {
          if (v.IDNum === data.id) {
            v.siNum = data.si_num

            // 保存答案
            if (!state.answer.updateEntAddStaff) {
              state.answer.updateEntAddStaff = []
            }
            state.answer.updateEntAddStaff.push(data.si_num)
          }
        })

      }
    },
    entMinusStaff(state, ms) {
      state.entInfo.entMinusStaff = ms
      
      if (!state.answer.entInfo) {
        state.answer.entInfo = {}
      }

      if (!state.answer.entInfo.entMinusStaff) {
        state.answer.entInfo.entMinusStaff = []
      }

      state.answer.entInfo.entMinusStaff = state.entInfo.entMinusStaff
    }
  }
